
nav {
  box-shadow: 0 1px 1px 0 rgba(0,0,0,0.14), 0 1px 2px 0 rgba(0,0,0,0.12), 0 1px 1px -2px rgba(0,0,0,0.2);
}
nav.nav-color {
  background: #fff;
}
a {
 color: rgb(255,64,129)
}
a.brand-logo {
  padding-left: 50px;
}
a.brand-logo, .nav-links {
  color: rgb(255,64,129);
}
div.table-data-row {
  border-left: 1px solid rgb(255,64,129)
}
span.chip-label {
    float: left;
    margin: 0 8px 0 -12px;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    text-align: center;
    color: #fff;
    font-weight: 600;
    font-size: 20px;
}
.chip {
  margin-left: 10%;
  min-width: 180px;
  overflow: hidden;
}
.td-chip {
  min-width: 300px;
}
.col.s2.button-spacing {
  width: 200px;
  margin-left: -40px;
}
.li-padding {
  border-left: 1px solid rgb(255,64,129);
  padding-left: 20px;
  margin-top: 5px;
  line-height: 2;
}
.suggestions-list {
  padding-left: 16px;
}
.wpps {
  color: #0073aa;
  font-weight: 600;
}
.web-sec {
  color: darkorchid;
  font-weight: 600;
}
.wp {
  color: #0073aa;
}
input#error-field:focus {
  border-bottom: 1px solid #304ffe;
  box-shadow: 0 1px 0 0 #304ffe;
}
input#error-field:focus+label {
  color: #304ffe;
}
button#submit-button {
  margin-top: 20px;
}
#errorMonkey {
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#nav-mobile {
  padding-right: 50px;
}
#version {
  color: rgba(48,79,254, .8);
  font-weight: lighter;
  padding: 10px;
  line-height: 1.75;
}



